import uniIcon from '../uni-icon/uni-icon.vue'
export default {
	name: 'UniPagination',
	components: {
		uniIcon
	},
	props: {
		prevText: {
			type: String,
			default: '上一页'
		},
		nextText: {
			type: String,
			default: '下一页'
		},
		current: {
			type: [Number, String],
			default: 1
		},
		total: { // 数据总量
			type: [Number, String],
			default: 0
		},
		pageSize: { // 每页数据量
			type: [Number, String],
			default: 10
		},
		showIcon: { // 是否以 icon 形式展示按钮
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			currentIndex: 1
		}
	},
	computed: {
		maxPage() {
			let maxPage = 1
			let total = Number(this.total)
			let pageSize = Number(this.pageSize)
			if (total && pageSize) {
				maxPage = Math.ceil(total / pageSize)
			}
			return maxPage
		}
	},
	watch: {
		current(val) {
			this.currentIndex = +val
		}
	},
	created() {
		this.currentIndex = +this.current
	},
	methods: {
		clickLeft() {
			if (Number(this.currentIndex) === 1) {
				return
			}
			this.currentIndex -= 1
			this.change('prev')
		},
		clickRight() {
			if (Number(this.currentIndex) === this.maxPage) {
				return
			}
			this.currentIndex += 1
			this.change('next')
		},
		change(e) {
			this.$emit('change', {
				type: e,
				current: this.currentIndex
			})
		}
	}
}
